import { useSelector } from "react-redux";
function Navigation() {
let data=useSelector(state => state.navigation);
	return(
		<>
		{
			data.map((d, i) =>
			<div key={i} id={d.device}>
			<ul>
				{
					d.navigation.map((d, i) => <li key={i}><a href="">{d}</a></li>)
				}
			</ul>
		</div>
			)
		}
		</>
	);
}
export default Navigation;



