import Sub3List from "./Sub3List";
import { useSelector } from "react-redux";
function Sub3Content(props){
	let data=props.propsValue;
	return(
		<>
		<div className="content">
				<ul>
					{/* <Sub3List /> */}
					{
						data.map((d,i)=><Sub3List key={i} propsValue={d} /> )
					}
				</ul>
			</div>
		</>
	);
}
export default Sub3Content;