import Title from "./Title";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
function Section3() {
	let navigate=useNavigate();
	let data=useSelector(state => state.title);
	return(
	<>
	<section id="section3">
			<div className="inner">
				<Title propsValue={data[2]} />
				<div className="mouse">
					<button data-aos-delay="300" onClick={()=>navigate("/portfoliolist")}><img src="./assets/images/arrow.svg" alt="" /></button>
				</div>
			</div>
	</section>
	</>
	);
}
export default Section3;



