import Section2List from "./Section2List";

function Section2Content(props) {
	let data=props.propsValue;

	return(
	<>
		<div data-aos="fade-up" data-aos-delay="500"  className="content">
				<ul>
					{
						data.map((d, i) => <Section2List key={i} propsValue={d}/>)
					}
				</ul>
			</div>
	</>
	);
}
export default Section2Content;



