function Section2List(props) {
	let {id, title, path}=props.propsValue;

	return(
		<li className="line">
			<p>{title}</p>
			<ul>
				{
					path.map((d, i)=><li key={i}><img src={"./assets/images/"+d+".svg"} alt={d} /></li>)
				}
			</ul>
		</li>
	);
}
export default Section2List;



