import { useSelector } from "react-redux";
import Section1Content from "./Section1Content";
import Title from "./Title";


function Section1() {
	let data=useSelector(state => state.title);
	let infor=useSelector(state => state.section1content);
	return(
	<>
	<section id="section1">
		<div className="inner">
			<Title propsValue={data[0]} />
			<div data-aos="fade-up" data-aos-delay="300"  className="content">
				<div className="visual">
					<img src="./assets/images/yua.svg" alt="" />
				</div>
				<Section1Content propsValue={infor}/>
			</div>
		</div>
	</section>
	</>
	);
}
export default Section1;



