
import Gnb from './Gnb';
import { useEffect } from 'react';
import { useNavigate } from "react-router-dom";
import ScrollToPlugin from 'gsap/ScrollToPlugin';
import ScrollTrigger from '@terwanerik/scrolltrigger';
import AOS from 'aos';
import 'aos/dist/aos.css';
import gsap from 'gsap';
import axios from 'axios';

function SubHeader(){
	let navigate=useNavigate();
	useEffect(()=>{
		gsap.registerPlugin(ScrollToPlugin);
		function portfolioListInit(){
			gsap.to(window,{scrollTo:0,duration:0});
		}
		portfolioListInit();

		let gnb=document.getElementById("gnb");
		let gnbList=gnb.firstElementChild.firstElementChild.children;
		// console.log(gnbList);

		let a=0;
		let total=gnbList.length;

		for(let i=0; i<total; i++){
			gnbList[i].addEventListener("click", e => {
				e.preventDefault();

				switch(i){
					case 0 : navigate("/", {state: "main"}); break;
					case 1 : navigate("/", {state: "section1"}); break;
					case 2 : navigate("/", {state: "section2"}); break;
					case 4 : navigate("/", {state: "section4"}); break;
					default : break;
				}
			});
		}

		let n=0;    //   
		let wint=0;//     
		let t=0;   //    
		let topPos=0; //  

		let header=document.getElementById("header");
		let main=document.getElementById("main");
		let sub=document.getElementById("sub");

		
		let section=document.querySelectorAll("section");

		let desktop=document.getElementById("desktop");
		let desktopList=desktop.firstElementChild.children;

		let tab=document.getElementById("tab");

		let mobile=document.getElementById("mobile");
		let mobileList=mobile.firstElementChild.children;

		let btnTop=document.querySelector(".btn_top");

	//------------------------#main  section collection push--------------------------------------------
		let pageList=[]; 
		pageList[0]=main;
		for(let i=0; i < section.length; i++){
			pageList.push(section[i]);
		}
		// ------------scroll_header.fixed-----------------
		window.addEventListener("scroll",function(){ 
			wint = document.documentElement.scrollTop; 
			if(wint > 50){ 
				if(header.classList.contains("fixed") === false){ 
					header.classList.add("fixed");
				}
			}
			else {
				if(header.classList.contains("fixed") === true){ 
				header.classList.remove("fixed"); 
				}
			}
		// ------------scroll_    -----------------
			t=window.scrollY;
			if(t <pageList[1].offsetTop){ 
				n=0; 
			}
			else if(t < pageList[2].offsetTop){ 
				n=1; 
				}
				else if(t < pageList[3].offsetTop){
				n=2; 
				}
				else if(t < pageList[4].offsetTop){ 
					n=3;
						if(window.innerHeight+t == document.body.scrollHeight){
						n=4;
						}
					}
					else{ 
					n=4; 
					}
	// --------------------scroll_btnTop--------------------------------
					if(n === 0){ 
						btnTop.classList.remove("active"); 
					}
					else { 
						btnTop.classList.add("active"); 
					}
		});
		btnTop.addEventListener("click",function(e){
			e.preventDefault();
			gsap.to(window,  {scrollTo:0, duration:0.4 });
		});
	//----------------------------mobile   -----------------------------------------------
		
		for (let i = 0; i < mobileList.length; i++) {
			mobileList[i].addEventListener("click", function (e) { 
				tab.classList.remove("active"); 
				mobile.removeAttribute("style"); 
				document.body.classList.remove("fixed"); 
			});
		}
	// ---------------mobile tab------------------------------------------------------------------------------
		let headerFlag = false; 
		tab.addEventListener("click", function(e){
			e.preventDefault();
			if(tab.classList.contains("active") === false){ 
				headerFlag=true; 
				document.body.classList.add("fixed"); 
				header.classList.add("active");
				tab.classList.add("active"); 
				gsap.fromTo(mobile, {display: "block", opacity: 0}, {opacity: 1, duration: 0.5}); 
			}
			else{ 
				headerFlag = false; 
				document.body.classList.remove("fixed");
				header.classList.remove("active"); 
				tab.classList.remove("active");
				mobile.removeAttribute("style");
			}
		});
	// ---------------resize-------------------------------------------------------------------------------
		window.addEventListener("resize",function(){
			let winw=window.innerWidth; 
			if(winw > 900){
				if(tab.classList.contains("active")){ 
					document.body.classList.remove("fixed");
					mobile.style.display="none"; 
					header.classList.remove("active"); 
					tab.classList.remove("active");
				}
			}
		});
	// ---------------------------------aos----------------------------------------------------------------
		AOS.init({
			easing: "ease-in-out-sine",
			duration: 700,
			once: true 
		});
	});
	return (
		<header id="header" className="sub_header">
		<div className="header_inner">
		<a href="/"><h1>logo</h1></a>
			<Gnb />
			<a href="" id="tab" className="open">
				<span className="burger-icon">
				<div className="burger-line"></div>
				<div className="burger-line"></div>
				<div className="burger-line"></div>
				</span>
			</a>
	</div>
	</header>
	);
}
export default SubHeader;

