import SubTitle from "./SubTitle";
import Sub3Content from "./Sub3Content";
import { useSelector } from "react-redux";
function Sub3(){
	let data=useSelector(state => state.subtitle);
	let infor=useSelector(state => state.sub3list);
	return(
		<>
		<section id="sub3">
		<div className="inner">
			<SubTitle propsValue={data[1]}/>
			<Sub3Content propsValue={infor}/>
		</div>
	</section>
		</>
	);
}
export default Sub3;