import Sub1Left from "./Sub1Left";
import Sub1Right from "./Sub1Right";

function Sub1List(props){
	let data=props.propsValue;
	return(
		<li>
			<div className="inner">
				<Sub1Left propsValue={data} />
				<Sub1Right propsValue={data} />
			</div>
		</li>
	);
}
export default Sub1List;