import SubTitle from "./SubTitle";
import OpenSwiper from "./OpenSwiper";
import { useSelector } from "react-redux";
function Sub2(){
	let data=useSelector(state => state.subtitle);
	return(
		<>
		<section id="sub2">
		<div className="inner">
			<SubTitle propsValue={data[0]}/>
			<OpenSwiper />
		</div>
		</section>
		</>
	);
}
export default Sub2;