import Sub1List from "./Sub1List";
import { useSelector } from "react-redux";
function Sub1Content(){
	let data=useSelector(state => state.sub1left);
	return(
		<>
		<div className="content">
				<ul>
					{
						data.map((d,i)=> <Sub1List key={i} propsValue={d}/> )
					}
				</ul>
			</div>
		</>
	);
}
export default Sub1Content;