function Section4List(props) {
	let data=props.propsValue;
	console.log(data);
	return(
	<>
	{ 
		data.map((d,i)=>
		<li key={i}>
			<a href={d.link} target="contact">
			<strong>{d.title}</strong>
			<span>{d.cont}</span>
			<span className="number">{d.num}</span>
			</a>
		</li>)
	}
	
	</>
	);
}
export default Section4List;



