import Title from "./Title";
import Section2Content from "./Section2Content";
import { useSelector } from "react-redux";
function Section2() {
		let data=useSelector(state => state.title);
		let infor=useSelector(state => state.section2list);
	return(
	<>
	<section id="section2">
		<div className="inner">
			<Title propsValue={data[1]} />
			<Section2Content propsValue={infor} />
		</div>
	</section>
	</>
	);
}
export default Section2;



