import Section4List from "./Section4List";

function Section4Content(props) {
	let data=props.propsValue;
	return(
	<>
	<div data-aos="fade-up" data-aos-delay="500" className="content">
					<ul>
					<Section4List propsValue={data} /> 
					</ul>
				</div>
	</>
	);
}
export default Section4Content;



