import { useSelector } from "react-redux";

function Title(props) {
    let data=props.propsValue;
	return(
	<>
	<div className="text">
        <h3 data-aos="fade-up" >{data.name}</h3>
        {
			data.desc.map((d,i)=> <span key={i} data-aos="fade-up" data-aos-delay="300">{d}</span> )
		}
    </div>
	</>
	);
}
export default Title;



