import MainTitle from "./MainTitle";
import { useSelector } from "react-redux";

function Main() {
	let data=useSelector(state => state.maintitle);
	return(
	<>
	<div id="main">
        <div className="inner">
        <MainTitle propsValue={data} />
        </div>
    </div>
	</>
	);
}
export default Main;



