import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay, Scrollbar } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/scrollbar';
import OpenSwiperList from './OpenSwiperList';

import { useSelector } from "react-redux";

function OpenSwiper(){
	let data=useSelector(state => state.openswiperlist);
	// console.log(data);

	return(
		<div id="container" className="container">
			<div className="sub_slider">
				<Swiper
					direction="horizontal"
					slidesPerView={1}
					spaceBetween={20}
					initialSlide={2}
					loop={true}
					scrollbar={{
						hide: false,
					}}
					autoplay={{
						delay: 3000,
						disableOnInteraction: false, 
					}}
					breakpoints={
						{
							420: {
								slidesPerView: 1,
								spaceBetween: 20
							},
							580: {
								slidesPerView: 2,
								spaceBetween: 20
							},
							820: {
								slidesPerView: 3,
								spaceBetween: 20
							},
							1350: {
								slidesPerView: 4,
								spaceBetween: 20
							}
						}
					}
					modules={[Scrollbar]}
					className="swiper"
				>
					{
						data.map((d, i)=>
							<SwiperSlide key={i}>
								<OpenSwiperList  propsValue={d} />
							</SwiperSlide>
						)
					}
				</Swiper>
			</div>
		</div>
	);
}
export default OpenSwiper;