import Title from "./Title";
import Section4Content from "./Section4Content";
import { useSelector } from "react-redux";
function Section4() {
	let data=useSelector(state => state.title);
	let infor=useSelector(state => state.section4list);
	return(
	<>
	<section id="section4">
			<a href="" className="btn_top"><i className="fa fa-angle-up"></i></a>
			<div className="inner">
				<Title propsValue={data[3]} />
				<Section4Content propsValue={infor} />
			</div>
	</section>
	</>
	);
}
export default Section4;



