
function Sub1Right(props){
	let {id,links}=props.propsValue;
	// console.log(links);

	return(
		<>
		<div className="right">
			<a href={links[0]} target="styleguide">STYLE GUIDE</a>
			<a href={links[1]} target="site" className="link">
				<p>LINK</p>
	{(id % 2 === 0) ? <img src={process.env.PUBLIC_URL+"/assets/images/Arrow9_1.png"} alt="" /> : <img src={process.env.PUBLIC_URL+"/assets/images/Arrow9.png"} alt="" />}
			</a>
		</div>
		</>
	);
}
export default Sub1Right;