
function OpenSwiperList(props){
	let data=props.propsValue;
	return(
		<>
		
			<div className="image"><img src={data.image} alt="" /></div>
			<div className="desc">
				<strong>
					{data.skilltitle} 
					{
						data.skills.map((d,i)=><p key={i}>{d}</p>)
					}
				</strong>
				<span>{data.desc}</span>
			</div>
			<div className="link">
				<a href={data.links[0]} target="site"><p>Web</p></a>
				<a href={data.links[1]} target="git"><p>GitHub</p></a>
			</div>
			</>
	);
}
export default OpenSwiperList;