function Sub3List(props){
	let data=props.propsValue;
	let {link,name,skill,date}=data;
	return(
		<>
		<li><a href={link}>
				<div className="right">
					<strong>{name}</strong> 
					<span className="skills">{skill}</span>
				</div>
				<div className="left">
					<div className="date">{date}</div>
				</div>
			</a>
		</li>
		</>
	);
}
export default Sub3List;