function MainTitle(props) {
	let data=props.propsValue;
	return(
	<>
	<div className="maintitle">
            <h2 data-aos="fade-up">{data.h2}</h2>
            <span data-aos="fade-up" data-aos-duration="400" data-aos-delay="300" >{data.span}</span>
        </div>
	</>
	);
}
export default MainTitle;



