function Sub1Left(props){
	let data=props.propsValue;
	let {num,subject,skilltitle,skills,desc}=data;
	return(
		<>
		<div className="left">
			<div className="left_left">
				<h2>{num}</h2>
			</div>
			<div className="left_right">
				<strong>{subject}</strong>
				<span className="skills">{skilltitle} - 
				{
					skills.map((d,i) =><p key={i}>{d}</p> )
				}
				</span>
				<div className="infor_cont">
					{
						desc.map((d,i)=><span key={i} className="infor">{d}</span>)
					}
				</div>
			</div>
		</div>
		</>
	);
}
export default Sub1Left;